        'Ð Ð¼ÐµÐ½Ñ âÐÑÐµÐ¼Ð¸ÑÐ¼â Ð¼Ñ Ð¸ÑÐ¿Ð¾Ð»ÑÐ·ÑÐµÐ¼ Ð½Ðµ ÑÐ¾Ð»ÑÐºÐ¾ ÐºÑÐ°ÑÐ¸Ð²ÑÐ¹ Ð´Ð¸Ð·Ð°Ð¹Ð½ ÑÐ¿Ð°ÐºÐ¾Ð²ÐºÐ¸, Ð½Ð¾ Ð¸ ÐºÐ°ÑÐµÑÑÐ²ÐµÐ½Ð½Ð¾Ðµ Ð¸ÑÐ¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ðµ Ð±Ð»ÑÐ´. ÐÑÐ°ÑÐ½Ð°Ñ ÑÑÐ±Ð°, Ð¼Ð¾ÑÐµÐ¿ÑÐ¾Ð´ÑÐºÑÑ, ÑÑÑÐºÑÑ - ÑÐµÑÑÐ¾ÑÐ°Ð½Ð½Ð¾Ðµ Ð¼ÐµÐ½Ñ Ð±ÐµÐ· Ð¿Ð¾ÑÐ¾Ð´Ð° Ð² ÑÐµÑÑÐ¾ÑÐ°Ð½!',
